"use client";
import { useEffect, useState } from "react";
import { useAuth } from "../../context/AuthContext";
import { useAuthGuard } from "../../hooks/useAuthGuard";
import EmployeeModal from "../../components/EmployeeModal";
import api from "../../services/api";

interface Employee {
	id: number;
	firstName: string;
	lastName: string;
	rate: number;
	workDays: number;
}

export default function EmployeesPage() {
  const { token } = useAuth();
  const { user, loading } = useAuthGuard();
  const [employees, setEmployees] = useState<Employee[]>([]);
  const [selectedEmployee, setSelectedEmployee] = useState<Employee | null>(null);

  // Fetch employees from backend
  const fetchEmployees = async () => {
    if (!token) return;
    try {
      const res = await api.get("/api/employees");
      setEmployees(res.data);
    } catch (err) {
      console.error("Failed to fetch employees", err);
    }
  };

  // Fetch on page load or when token/user changes
  useEffect(() => {
    if (token && user) fetchEmployees();
  }, [token, user]);

  // Callback for modal submit
  const handleSave = async (employee: Employee) => {
    try {
      if (employee.id && employee.id !== 0) {
        // Edit
        await api.put(`/api/employees/${employee.id}`, employee);
      } else {
        // Add new
        await api.post("/api/employees", employee);
      }
      fetchEmployees(); // refresh list
    } catch (err) {
      console.error(err);
      alert("Failed to save employee");
    }
  };

  // Delete employee
  const handleDelete = async (id: number) => {
    if (!confirm("Are you sure?")) return;
    try {
      await api.delete(`/api/employees/${id}`);
      fetchEmployees();
    } catch (err) {
      console.error(err);
      alert("Failed to delete employee");
    }
  };

  if (loading) return <p>Loading...</p>;
  if (!user) return <p>Redirecting...</p>;

return (
	<div className="container py-5">
		<h2>Employees</h2>
			<EmployeeModal
			onSubmit={handleSave}       // callback to update backend & fetch employees
			buttonText="Add Employee"   // text on button
			/>
	  		<table className="table table-striped">
			<thead>
				<tr>
					<th>Name</th>
					<th>Last Name</th>
					<th>Rate</th>
					<th>Work Days</th>
					<th>Actions</th>
				</tr>
			</thead>
			<tbody>
				{employees.map((emp) => (
					<tr key={emp.id}>
						<td>{emp.firstName}</td>
						<td>{emp.lastName}</td>
						<td>{emp.rate}</td>
						<td>{emp.workDays}</td>
						<td>
							<EmployeeModal
							employee={selectedEmployee}
							isEdit={true}
							onSubmit={handleSave}
							buttonText="Edit Employee"
							/>
							<button className="btn btn-danger" onClick={() => handleDelete(emp.id)}>
							Delete
							</button>
						</td>
					</tr>
				))}
			</tbody>
		</table>

	</div>
);
}
