"use client";

import { useState, useEffect } from "react";

export interface Employee {
	id: number;
	firstName: string;
	lastName: string;
	rate: number;
	workDays: number;
}

interface EmployeeModalProps {
	employee?: Employee | null;
	isEdit?: boolean;
	onSubmit: (employee: Employee) => void;
	buttonText?: string;
	className?: string;
}

export default function EmployeeModal({
	employee,
	isEdit = false,
	onSubmit,
	buttonText = "Add Employee",
	className = "btn btn-primary mb-3",
}: EmployeeModalProps) {
	const [showModal, setShowModal] = useState(false);
	const [modalEmployee, setModalEmployee] = useState<Employee>(
		employee || { id: 0, firstName: "", lastName: "", rate: 0, workDays: 0 }
	);
	const [fadeIn, setFadeIn] = useState(false);

	console.log(employee);

	const openModal = () => {
		setModalEmployee(employee || { id: 0, firstName: "", lastName: "", rate: 0, workDays: 0 });
		setShowModal(true);
	};

	const closeModal = () => {
		setFadeIn(false);
		setTimeout(() => setShowModal(false), 300);
	};

	useEffect(() => {
		if (showModal) {
			  if (employee) {
    setModalEmployee(employee); // update state when editing
  } else {
    setModalEmployee({ id: 0, firstName: "", lastName: "", rate: 0, workDays: 0 });
  }
		setTimeout(() => setFadeIn(true), 20);
		}
	}, [showModal, employee]);

	const handleSubmit = (e: React.FormEvent) => {
		e.preventDefault();
		onSubmit(modalEmployee);
		closeModal();
	};

	return (
		<>
			{/* Open button */}
			<button className={className} onClick={openModal}>
				{buttonText}
			</button>

			{/* Modal */}
			{showModal && (
				<>
				{/* Backdrop */}
				<div
					className={`modal-backdrop fade ${fadeIn ? "show" : ""}`}
					style={{ transition: "opacity 0.3s" }}
				/>

				<div
					className={`modal fade ${fadeIn ? "show d-block" : "d-none"}`}
					tabIndex={-1}
					style={{ transition: "opacity 0.3s" }}
				>
					<div className="modal-dialog">
						<div className="modal-content">
							<form onSubmit={handleSubmit}>
								<div className="modal-header">
									<h5 className="modal-title">{isEdit ? "Edit Employee" : "Add Employee"}</h5>
									<button type="button" className="btn-close" onClick={closeModal} />
								</div>
								<div className="modal-body">
									<input
										className="form-control mb-2"
										placeholder="firstName"
										value={modalEmployee.firstName}
										onChange={(e) =>
											setModalEmployee({ ...modalEmployee, firstName: e.target.value })
										}
										required
									/>
									<input
										className="form-control mb-2"
										placeholder="Last firstName"
										value={modalEmployee.lastName}
										onChange={(e) =>
											setModalEmployee({ ...modalEmployee, lastName: e.target.value })
										}
										required
									/>
									<input
										className="form-control mb-2"
										type="number"
										placeholder="Rate"
										value={modalEmployee.rate}
										onChange={(e) =>
											setModalEmployee({ ...modalEmployee, rate: parseFloat(e.target.value) })
										}
										required
									/>
									<input
										className="form-control"
										type="number"
										placeholder="Work Days"
										value={modalEmployee.workDays}
										onChange={(e) =>
											setModalEmployee({ ...modalEmployee, workDays: parseInt(e.target.value) })
										}
										required
									/>
								</div>
								<div className="modal-footer">
									<button type="button" className="btn btn-secondary" onClick={closeModal}>
										Close
									</button>
									<button type="submit" className="btn btn-primary">
										{isEdit ? "Save Changes" : "Add Employee"}
									</button>
								</div>
							</form>
						</div>
					</div>
				</div>
				</>
			)}
		</>
	);
}
